@import "../scss/styles-variables.scss";

.section {
  width: 90vw;
  margin: 0 auto;
  max-width: var(--max-widthnav);
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}
/*
 =============== 
 Navbar
 ===============
 */
.navbar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
}
.navbar-toggle {
  font-size: 1.5rem;
  color: $mainColor;
  background: transparent;
  border-color: transparent;
  transition: var(--transition);
  cursor: pointer;
}
.navbar-toggle:hover {
  color: var(--mainText);
  transform: rotate(90deg);
}
.logo {
  width: 170px;
  height: 100px;
  object-fit: cover;
  border-radius: 70%;
  box-shadow: var(--lightShadow);
  @media (max-width: $md2+px) {
    width: 130px;
    height: 70px;
  }
}

.social-icons {
  display: none;
}
.navlinks-container {
  height: 0;
  overflow: hidden;
  transition: var(--transition);
}
.show-container {
  height: 10rem;
}
@media screen and (min-width: 800px) {
  .navbar-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
  }
  .navbar-header {
    padding: 0;
  }
  .navbar-toggle {
    display: none;
  }
  .navlinks-container {
    height: auto !important;
  }
  .navlinks {
    display: flex;
  }
  .navlinks a {
    padding: 0;
    margin: 0 0.5rem;
    transition: all 400ms ease;
  }
}
.nav-link {
  color: var(--mainText);
  font-size: 23px;
  letter-spacing: var(--spacing);
  display: block;
  padding: 4px 6px;
  transition: var(--transition);
  @media (max-width: $md2+px) {
    padding: 3px 5px;
    font-size: 20px;
  }
}
.nav-link:hover {
  color: $mainColor;
}
.nav-selected {
  color: $mainColor;
  transition: all 400ms ease;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  @media (max-width: $md2+px) {
    padding: 0px 0px 0px 50px;
    font-size: 20px;
  }
}
