@import "../scss/styles-variables.scss";
.header {
  &__menu {
    padding: 0 0 0 3.15789%;
    flex: 1 1 auto;

    @media (max-width: $md3+px) {
      padding: 0;
    }
  }
}
.menu {
  &__list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @media (max-width: $md2+px) {
      justify-content: space-between;
    }
  }
  &__link-1 {
    display: flex;
    align-items: center;
    margin: 0px 32px 0px 0px;
    max-width: 200px;
    position: relative;
    padding: 0px 0px 0px 30px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 22px;
    color: #fff;
    @media (max-width: 600px) {
    }
  }
  &__link {
    display: flex;
    align-items: center;
    margin: 0px 0px 0px 0px;
    max-width: 200px;
    position: relative;
    padding: 0px 0px 0px 30px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 22px;
    color: #fff;
    @media (max-width: $md3+px) {
      margin: 0px 0px 0px 0px;
      padding: 0px 0px 0px 15px;
    }
  }
}
.goto-block {
  overflow: hidden;
  margin: 0px 0px 0px 0px;
  white-space: nowrap;
  position: relative;
  color: #fff;
  border-radius: 8px;
  padding: 3px 13px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background: rgb(230, 150, 6);
  background: linear-gradient(
    90deg,
    rgba(230, 150, 6, 0.9856092095041141) 0%,
    rgba(255, 61, 0, 1) 100%
  );
  @media (max-width: 500px) {
    display: none;
  }
  @media (max-width: 360px) {
    padding: 2px 9px;
  }
  &:hover {
    cursor: pointer;
    background: rgb(230, 150, 6);
    background: linear-gradient(
      90deg,
      rgba(228, 157, 25, 0.986) 0%,
      rgb(255, 94, 0) 100%
    );
    box-shadow: 0px 16px 24px rgba(38, 38, 38, 0.2),
      0px 8px 16px rgba(38, 38, 38, 0.24);
  }
}
.__goto-block {
  padding: 0px -5px 0px 0px;
  background: url("../img-main/img-header/clock.png") left no-repeat;
  height: 30px;
  background-size: contain;
  cursor: default;
  @media (max-width: $md2+px) {
    display: none;
  }
}
._goto-block {
  padding: 0px -5px 0px 0px;
  height: 24px;
  background: url("../img-main/img-header/phone.png") left no-repeat;
  background-size: contain;
  cursor: pointer;
  @media (max-width: $md4+px) {
    background: none;
  }
}
._goto-blockHover {
  @media (max-width: $md4+px) {
    font-size: 10px;
  }
}
/* icons stule , сами иконки в футере */
.list-icon-header {
  display: flex;

  align-items: center;

  a {
    width: 22px;
    height: 22px;
    margin: 0px 0px 0px 20px;
    background-size: cover;
    display: inline-block;
    cursor: pointer;
    filter: invert(51%) sepia(105%) saturate(3000%) hue-rotate(367deg)
      brightness(119%) contrast(99%);
    @media (max-width: $md4+px) {
      margin: 0px 0px 0px 10px;
    }
    &:hover {
      filter: invert(2) invert(51%) sepia(105%) saturate(3000%)
        hue-rotate(350deg) brightness(119%) contrast(99%);
    }
  }
  a:first-child {
    margin: 0px 0px 0px 40px;
    @media (max-width: $md2+px) {
      margin: 0px 0px 0px 20px;
    }
  }
  a:last-child {
    margin: 0px 40px 0px 20px;
    width: 33px;
    height: 33px;
    @media (max-width: $md2+px) {
      margin: 0px 20px 0px 20px;
    }
    @media (max-width: $md4+px) {
      margin: 0px 10px 0px 10px;
    }
  }
}
/* блик для кнопки */
.autoflash {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.flash {
  content: "";
  background-color: rgba(255, 255, 255, 0.272);
  height: 100%;
  width: 3em;
  display: block;
  position: absolute;
  top: 0;
  left: -4.5em;
  -webkit-transform: skewX(-45deg) translateX(0);
  transform: skewX(-45deg) translateX(0);
  -webkit-transition: none;
  transition: none;
}

.lighting {
  webkit-animation: moving 4s ease-in-out infinite;
  -moz-animation: moving 4s ease-in-out infinite;
  -ms-animation: moving 4s ease-in-out infinite;
  -o-animation: moving 4s ease-in-out infinite;
  animation: moving 4s ease-in-out infinite;
}

@keyframes moving {
  30% {
    webkit-transform: skewX(-45deg) translateX(33.5em);
    transform: skewX(-45deg) translateX(33.5em);
  }

  100% {
    webkit-transform: skewX(-45deg) translateX(33.5em);
    transform: skewX(-45deg) translateX(33.5em);
  }
}
