@import "../scss/styles-variables.scss";

/* section */
.section-slider {
  margin: 2rem auto;
  margin-top: 0px;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section-slider {
  }
}
/*
=============== 
Slider
===============
*/

.section-center {
  margin: 0 auto;
  width: 80vw;
  min-height: 540px;
  max-width: 800px;
  text-align: center;
  position: relative;
  display: flex;
  overflow: hidden;
}
.person-left {
  cursor: pointer;
  position: absolute;
  left: 0;
  width: 370px;
  height: 392px;
}
.person-right {
  cursor: pointer;
  position: absolute;
  right: 0;
  width: 370px;
  height: 392px;
}
.person-img {
  margin-bottom: 1rem;
  width: 642px;
  height: 392px;
  object-fit: cover;
  border: 4px solid $mainGreyColor;
  box-shadow: $mainGreyColor;
  @media (max-width: $md2+px) {
    width: 442px;
    height: 292px;
  }
  @media (max-width: 555px) {
    width: 360px;
    height: 240px;
  }
}
article h4 {
  text-transform: uppercase;
  color: $mainColor;
  margin-bottom: 0.25rem;
}

.icon {
  font-size: 3rem;
  margin-top: 1rem;
  color: $mainColor;
}
.prev,
.next {
  position: absolute;
  top: 200px;
  transform: translateY(-50%);
  background: $mainGreyColor;
  color: var(--clr-white);
  width: 1.25rem;
  height: 1.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
}
.prev:hover,
.next:hover {
  background: $mainColor;
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
@media (min-width: 800px) {
  .text {
    max-width: 45em;
  }
  .prev,
  .next {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}
article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}
article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}
