@import "../scss/styles-variables.scss";

.container-grid {
  width: 90vw;
  margin: 5rem auto;
  margin-top: 0px;
  background-color: $mainGreyColor;
  border-radius: 0.25rem;
  padding: 2.5rem 2rem;
  max-width: var(--fixed-width);
  display: grid;
  gap: 1rem 2rem;
  box-shadow: var(--lightShadow);
  @media (max-width: $md2+px) {
    width: 80vw;
  }
}
.container-grid div {
  line-height: 1.2;
  font-weight: 500;
  font-size: 23px;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .container-grid {
    display: grid;
    grid-template-columns: 250px 1fr;
  }
}
.question {
  background-color: #fff;
  padding: 1rem 1.5rem;
  border: 2px solid var(--clr-grey-special);
  margin-bottom: 1rem;
  border-radius: var(--radius);
  box-shadow: var(--light-shadow);
  cursor: pointer;
}
.question-title {
  color: var(--clr-grey-3);
  padding: 4px 4px 4px 4px;
}
.question p {
  color: var(--clr-grey-3);
  margin-bottom: 0;
  margin-top: 0.5rem;
  background: var(--clr-grey-special);
  padding: 4px 4px 4px 4px;
}
.question header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.question header h4 {
  margin-bottom: 0;
}
.btn-q {
  background: transparent;
  border-color: transparent;
  width: 2rem;
  height: 2rem;
  background: var(--clr-grey-special);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: $mainColor;
  cursor: pointer;
  margin-left: 1rem;
  align-self: center;
  min-width: 2rem;
  &:hover {
    background: $mainColor;
    color: #ffff;
    transition: color 0.3s, background 0.3s;
  }
}
