@import "../scss/styles-variables.scss";
.Logo {
  &__body {
    margin: 0px 5px 15px 0px;
    width: 160px;
    height: 120px;
    font-family: "Quicksand", sans-serif;

    transition: all 1.2s ease;
    &:hover div {
      filter: blur(5px);
      -webkit-filter: blur(5px);
    }
  }
  &__bg {
    position: absolute;
    width: 160px;
    height: 120px;
    background: url("../img-main/bgSun.jpg") 0 0 no-repeat;
    background-size: cover;
    border-radius: 50%;
  }
  &__title {
    position: relative;
    width: 170px;
    height: 130px;

    font-size: 33px;
    text-align: center;
    &:hover span:nth-child(odd) {
      letter-spacing: 0;
    }
    &:hover i::before {
      top: 35px;
      content: "системи";
    }
    &:hover i::after {
      width: 170px;
      height: 130px;
      opacity: 1;
      visibility: visible;
    }

    span {
      position: relative;
      display: inline-flex;
      background: #61212e;
      background: -webkit-linear-gradient(
        to right,
        #61212e 0%,
        #e31b29 30%,
        #ff8205 60%,
        #fff01f 100%
      );
      background: -moz-linear-gradient(
        to right,
        #61212e 0%,
        #e31b29 30%,
        #ff8205 60%,
        #fff01f 100%
      );
      background: linear-gradient(
        to right,
        #61212e 0%,
        #e31b29 30%,
        #ff8205 60%,
        #fff01f 100%
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    span:nth-child(odd) {
      color: #414141;
      line-height: 1em;
      letter-spacing: -1em;
      overflow: hidden;
      transition: 0.9s ease-in-out;
    }
    /*     span:nth-child(2) {
      margin-right: 10px;
    }
    span:nth-child(2):before {
      content: "2";
      position: absolute;
      bottom: 0;
      right: -10px;
      font-size: 2em;
    } */
    i {
      color: #fff01f;
      position: absolute;
      transform: translateX(-50%);
      font-size: 18px;
      font-weight: 400;
      font-style: initial;
      line-height: 1em;
      &:before {
        position: absolute;
        text-align: center;
        content: "Lviv";
        width: 100%;
        display: initial;
        transition: 1.2s;
        font-size: 20px;
        top: 0;
      }
      &:after {
        content: "Сонцезахисні";

        opacity: 0;
        visibility: hidden;
        transition: 1.2s;
        font-size: 20px;
      }
    }
  }
}
