.coment {
  &__body {
    position: relative;
    padding: 0px 15px 15px 15px;
    display: flex;
    flex-direction: column;
    min-width: 400px;
    width: 400px;
    background-color: var(--mainBg);
  }
  &__name {
    padding: 20px 5px 0px 5px;
    font-weight: bold;
    color: var(--clr-font);
    font-size: 16px;
    text-align: center;
  }
  &__text {
    margin: 0px 0px 10px 0px;
    text-indent: 15px;
    padding: 20px 5px 0px 5px;
    font-weight: normal;
    color: var(--clr-font);
    font-size: 14px;
  }
}
