@import "../scss/styles-variables.scss";
@import "../scss/Loader.scss";
.dark-theme {
  --clr-bcg: #282c35;
  --clr-font: #fff;
  --clr-primary: #ffa7c4;
  --clr-background: url("../img-main/ikon/Sun.png");
  --mainText: #fff;
  --mainBg: rgba(118, 113, 113, 0.076);
}
.light-theme {
  --clr-bcg: #fff;
  --clr-font: #282c35;
  --clr-primary: #d23669;
  --clr-background: url("../img-main/ikon/Moon.png");
  --mainText: #373737;
  --mainBg: rgba(49, 41, 41, 0.076);
}
/* 
====== 
Variables 
======
*/

/* 
====== 
Global Styles 
======
*/
//<Подключаем шрифты>=======================================================================================
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;0,700;0,900;1,900&display=swap");
//</Подключаем шрифты>=======================================================================================

//<Иконочные шрифты>==========================================================================================

//</Иконочные шрифты>==========================================================================================

//<Шрифт по умолчанию>==========================================================================================
$fontfamily: "Roboto", "sans-serif";
//</Шрифт по умолчанию>==========================================================================================
hr {
  border: none; /* Убираем границу */
  background-color: $mainGreyColor; /* Цвет линии */
  color: $mainGreyColor; /* Цвет линии для IE6-7 */
  height: 1px; /* Толщина линии */
  margin: 15px 0px 0px 0px;
}
.form * {
  outline: none;
}
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
aside,
nav,
footer,
header,
section {
  display: block;
}
html,
body {
  height: 100%;
  min-width: $minwidth;
}
body {
  line-height: 1;
  font-family: $fontfamily;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  /*   color: var(--mainBlack);
  background: var(--mainBackground); */
  background: var(--clr-bcg);
  color: var(--clr-font);
  line-height: 1.4;
  font-size: 1rem;
  font-weight: 300;
  overflow-y: scroll;
  scrollbar-width: none;
  transition: all 0.3s linear;
  -ms-overflow-style: none;
  height: calc(100vh - 60px);

  &::-webkit-scrollbar {
    width: 2px;
  }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px var(--mainBackground);
    -webkit-border-radius: 10px;
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: $mainGreyColor;
  }
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background-color: Black;
  opacity: 0.9;
}

.popup {
  position: absolute;
  width: 50%;
  background-color: #8da5c6;
  z-index: 999;
}
.fon {
  position: relative;
  z-index: 501;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  background: url("../img-main/ролокасетаю.png") no-repeat;
  background-size: containe;
}
.ReactModal__Overlay {
  transition: all 5000ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 1;
  transform: translateY(-110vh);
}

input,
button,
textarea {
  font-family: $fontfamily;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.container {
  padding: 4rem 0 0 0;
  max-width: $mw + px;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
  @media (max-width: $md1+px) {
    max-width: 980px;
  }
  @media (max-width: $md2+px) {
    max-width: 750px;
  }
  @media (max-width: $md3+px) {
    max-width: none;
    padding: 2.5rem 0 0 0;
  }
}
.container-main {
  padding: 0px 0px 0px 0px;
  max-width: $mw + px;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
  @media (max-width: $md1+px) {
    max-width: 980px;
  }
  @media (max-width: $md2+px) {
    max-width: 750px;
  }
  @media (max-width: $md3+px) {
    max-width: none;
    padding: 2rem 0 0 0;
  }
}
.bg-sunlight {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 20px;
  top: 20px;
  background: url("../img-main/FonGreen.jpg") center no-repeat;
  background-size: contain;
}

/* 
====== 
Title
======
*/
.title {
  text-align: center;
  font-size: 25px;
  font-weight: 500;
  color: var(--clr-font);
  margin-bottom: 40px;
  @media (max-width: $md3+px) {
    margin-bottom: 30px;
  }
}
.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}
.title span {
  font-weight: 700;
  text-align: center;
  font-size: 25px;
}
.text {
  max-width: 35em;
  margin: 0 auto;
  margin-top: 2rem;
  line-height: 2;
  color: var(--clr-font);
}
/* 
====== 
Buttons 
======
*/
.btn-them {
  width: 25px;
  height: 25px;
  background: var(--clr-background);
  background-size: contain;
  display: inline-block;
  padding: 0.45rem 0.8rem;
  margin: 0px 0px 0px 10px;
  filter: invert(51%) sepia(105%) saturate(3000%) hue-rotate(367deg)
    brightness(119%) contrast(99%);
  @media (max-width: $md4+px) {
    margin: 0px 0px 0px 10px;
  }
  &:hover {
    filter: invert(2) invert(51%) sepia(105%) saturate(3000%) hue-rotate(350deg)
      brightness(119%) contrast(99%);
  }
}
.btn,
.btn-white,
.btn-primary {
  text-transform: uppercase;
  letter-spacing: var(--mainSpacing);
  color: var(--primaryColor);
  border: 2px solid var(--primaryColor);
  padding: 0.45rem 0.8rem;
  display: inline-block;
  transition: var(--mainTransition);
  cursor: pointer;
  font-size: 0.8rem;
  background: transparent;
  border-radius: var(--mainBorderRadius);
  display: inline-block;
}
.btn:hover {
  background: $mainColor;
  color: var(--mainWhite);
}
.btn-white {
  background: transparent;
  color: var(--mainWhite);
  border-color: var(--mainWhite);
}
.btn-white:hover {
  background: $mainColor;
  color: var(--primaryColor);
}
.btn-primary {
  background: var(--primaryColor);
  color: var(--mainWhite);
  border-color: transparent;
}
.btn-primary:hover {
  background: $mainColor;
  color: var(--primaryColor);
}
.btn-block {
  width: 100%;
  display: block;
  margin: 0 auto;
  box-shadow: var(--lightShadow);
  text-align: center;
}
.btn-details {
  padding: 0.25rem 0.4rem;
}
.btn-details:hover {
  background: var(--primaryLightColor);
  border-color: var(--primaryLightColor);
}
/* 
====== 
Navbar
======
*/
.navbar {
  background: var(--mainWhite);
  height: 35px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid var(--primaryColor);
  box-shadow: var(--lightShadow);
  /*   background: url("../../jalousie/src/FonGreen.jpg") 0 0 no-repeat; */
  background-color: #5d5e5f;
  background-size: cover;
}

.nav-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0px 0px 0px 0px;
  max-width: $mw + px;
  margin: 0 auto;
  width: 100%;

  @media (max-width: $md1+px) {
    max-width: 980px;
  }
  @media (max-width: $md2+px) {
    max-width: 750px;
  }
  @media (max-width: $md3+px) {
    max-width: none;
    padding: 0 10px;
  }
}
.nav-links {
  display: flex;
  align-items: center;
}
.nav-links a {
  display: inline-block;
  font-weight: bold;
  margin-right: 5px;
  font-weight: 400;
  letter-spacing: 2px;
  font-size: 20px;
  padding: 0.25rem 0.5rem;
  transition: var(--mainTransition);
  color: #373737;
}
.nav-links a:hover {
  color: $mainColor;
}
.logo {
  width: 12rem;
}

/* 
====== 
Error
======
*/
.error-page {
  display: flex;
  justify-content: center;
}
.error-container {
  text-align: center;
  text-transform: capitalize;
}
.section {
  padding: 5rem 0 0 0;
}
.section-title {
  font-size: 2rem;
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
  text-align: center;
  margin-bottom: 3.5rem;
}
/* 
====== 
Form
======
*/
.search {
  margin-top: 1rem;
  padding-bottom: 0;
}

.search-form {
  width: var(--smallWidth);
  margin: 0 auto;
  max-width: var(--maxWidth);
  background: var(--mainWhite);
  padding: 2rem 2.5rem;
  text-transform: capitalize;
  border-radius: var(--mainBorderRadius);
  box-shadow: var(--lightShadow);
}

.form-control label {
  display: block;
  margin-bottom: 1.25rem;
  font-weight: bold;
  letter-spacing: 0.25rem;
  color: var(--primaryColor);
}
.form-control input {
  width: 100%;
  border: none;
  border-color: transparent;
  background: var(--mainBackground);
  border-radius: var(--mainBorderRadius);
  padding: 0.5rem;
  font-size: 1.2rem;
}

.loader,
.loader:before,
.loader:after {
  background: transparent;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: var(--primaryColor);
  text-indent: -9999em;
  margin: 88px auto;
  margin-top: 20rem;
  position: relative;
  font-size: 3rem;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* infoPage */
.drink {
  width: var(--smallWidth);
  max-width: var(--fullWidth);
  margin: 0 auto;
  text-align: left;
}
.drink img {
  border-radius: var(--mainBorderRadius);
}
.drink p {
  font-weight: bold;
  text-transform: capitalize;
  line-height: 1.8;
  padding: 10px 0px 0px 0px;
}
.drink span {
  margin-right: 0.5rem;
}
.drink-data {
  background: rgba(255, 105, 18, 0.523);
  padding: 0.25rem 0.5rem;
  border-radius: var(--mainBorderRadius);
  color: var(--clr-font);
}
.drink-info {
  padding-top: 2rem;
}
@media screen and (min-width: 992px) {
  .drink {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 3rem;
    align-items: center;
  }
  .drink-info {
    padding-top: 0;
  }
}
